.subordinate-navbar {
    background-color: #333;
    color: #fff;
    padding: 10px;
  }
  
  .navbar-links {
    display: flex;
    justify-content: flex-end;
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .navbar-links li {
    margin-left: 20px;
  }
  
  .navbar-links a {
    color: #fff;
    text-decoration: none;
    font-size: 16px;
  }
  